body{font-family:'PT Sans Narrow', sans-serif;font-size:15px;background:url("https://frapwithfriends1-seeds.s3.amazonaws.com/dota2heroes.jpg") no-repeat center center fixed;background-blend-mode:screen, difference, lighten;background-size:cover}.hidden-arc{fill:none}.main-arc{stroke:white;stroke-width:2px;transition:opacity .4s}.main-arc:hover{cursor:pointer}svg{display:flex;justify-content:center;align-items:center;opacity:.95}.heroes{padding:10px 25px;position:absolute;border-radius:5px;top:35%;font-size:17px;width:8%;left:5%;border:1.5px solid black;font-weight:500;cursor:pointer}.heroes:hover{background-color:#9e9e9e;color:#e6e6e6;transition:.7s}.heroes:focus{transition:.7s;color:white;background-color:black;border:1.5px solid #e6e6e6;outline:none}.matches{font-weight:500;padding:10px 25px;position:absolute;border-radius:5px;top:35%;left:14%;width:8%;font-size:17px;border:1.5px solid black;cursor:pointer}.matches:hover{background-color:#9e9e9e;color:#e6e6e6;transition:.7s}.matches:focus{transition:.7s;color:white;background-color:black;border:1.5px solid #e6e6e6;outline:none}.linkedin{position:absolute;bottom:23.5%;right:13%}.linkedin-img{width:52px;height:52px}.github{position:absolute;bottom:24%;right:20%}.github-img{height:45px}.poster{position:absolute;display:flex;flex-direction:column;align-items:center;width:20%;height:48%;background-color:#f1e9c6;top:22%;right:8%;opacity:.9;border-radius:10px}hr{display:block;position:relative;padding:0;margin:8px auto;height:0;width:45%;max-height:0;font-size:1px;line-height:0;clear:both;border:none;border-top:1px solid #1f3833;border-bottom:1px solid #ffffff}.title{font-size:24px;margin-top:10%;font-weight:700}.content{width:80%;line-height:24px}.credit{font-size:12px}.select-one{background:white;position:absolute;top:27%;left:9%;padding:10px 40px;border-radius:5px;background-color:#f1e9c6;opacity:.9}.slice:first-child{opacity:.3}.puzzled{position:absolute;top:41%;opacity:.95;left:12%;cursor:help;opacity:.95}.puzzled .puzzledtext{visibility:hidden;width:300px;background-color:black;color:#fff;text-align:center;padding:5px 0;border-radius:6px;position:absolute;z-index:1}.puzzled:hover .puzzledtext{visibility:visible}.puzzled .puzzledtext{padding:15px;background-color:white;color:black;width:300px;top:100%;left:50%;opacity:.95;line-height:20px;margin-left:-167px}
